.how {
  margin-top: 112px;
  padding-top: 50px;
  padding-bottom: 92px;
  background-color: @lLight;
  @media(max-width: 768px) {
    padding-bottom: 28px;
  }
  &__title {
    letter-spacing: -0.42px;
    line-height: 68px;
  }
  .subtitle {
    max-width: 540px;
    margin-left: auto;
    margin-right: auto;
  }
  &__sheme {
    margin-top: 92px;
    margin-left: 50px;
    @media(max-width: 991px) {
      margin-left: 0;
    }
    @media(max-width: 768px) {
      margin-left: 24px;
      margin-top: 69px;
    }
    &__arrow {
      position: absolute;
      right: -6px;
      top: -42px;
      font-size: 30px;
      @media(max-width: 768px) {
        font-size: 17px;
        right: auto;
        top: auto;
        left: -5px;
        bottom: -7px;
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        -o-transform: rotate(90deg);
        transform: rotate(90deg);
      }
    }
    &__num {
      font-family: FedraSansAltProBold;
      font-size: 14px;
      color: @red;
      letter-spacing: 0;
      line-height: 22px;
    }
    &__descript {
      font-family: FedraSansAltProBookTF;
      font-size: 14px;
      color: @dark;
      letter-spacing: 0;
      line-height: 22px;
      max-width: 400px;
    }
    &__step {
      float: left;
      width: 25%;
      position: relative;
      padding-left: 18px;
      border-left: solid 2px @red;
      height: 120px;
      padding-right: 20px;
      @media(max-width: 768px) {
        height: 170px;
        padding-top: 18px;
        width: 100%;
        border-width: 1px;
        border-color: @lightGrey;
        padding-left: 26px;
        &_dashed {
          border-left: 1px dashed;
        }
      }
      &_dashed {
        &:after {
          border-style: dashed;
        }
      }
      &_solid {
        &:after {
          border-style: solid;
        }
      }
      &:after {
        top: -28px;
        left: 0;
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        border-width: 1px;
        border-bottom: @lightGrey;
        border-color: @lightGrey;
        z-index: 1;
        @media(max-width: 768px) {
          top: 0;
          width: 120px;
          height: 2px;
          border: none;
          background-color: @red;
          left: 26px;
        }
      }
      &:before {
        z-index: 2;
        width: 13px;
        height: 13px;
        background-color: @red;
        border-radius: 50%;
        content: '';
        position: absolute;
        left: -7px;
        top: -35px;
        box-shadow: 0 0 0 4px @lLight, 0 0 0 5px @lightGrey;
        @media(max-width: 768px) {
          top: -7px;
        }
      }
    }
  }
}