.tv {
  margin-top: 103px;
  background-color: @lLight;
  position: relative;
  z-index: 4;
  padding-bottom: 29px;
  @media (max-width: 768px) {
    margin-top: 48px;
    padding-bottom: 60px;
  }
  &__title {
    line-height: 42px;
    @media (max-width: 768px) {
      line-height: 34px;
    }
  }
  &__descript {
    color: @dark;
    width: 380px;
    margin-top: 36px;
    @media (max-width: 1024px) {
      width: 100%;
    }
    @media (max-width: 768px) {
      margin-top: 67px;
    }
    &__title {
      font-family: FedraSansAltProMedTF;
      font-size: 30px;
      line-height: 38px;
      margin-bottom: 27px;
    }
    &__subtitle {
      font-family: FedraSansAltProBookTF;
      font-size: 15px;
      line-height: 26px;
    }
  }
  &__image {
    margin-top: -14px;
    width: 635px;
    @media (max-width: 1280px) {
      width: 580px;
    }
    @media (max-width: 1024px) {
      margin-top: 20px;
      width: 100%;
    }
  }
}
.tv__store {
  margin-top: 38px;
  @media (max-width: 768px) {
    margin-top: 25px;
  }
}
.app-store {
  margin-left: 5px;
  display: inline-block;
  background-image: url(../images/appstore.png);
  width: 55px;
  height: 61px;
  background-size: 52px auto;
  background-repeat: no-repeat;
  @media (max-width: 768px) {
    width: 44px;
    height: 50px;
    background-size: 44px auto;
  }
}
.google-play {
  margin-left: 18px;
  display: inline-block;
  background-image: url(../images/googleplay.png);
  width: 71px;
  height: 71px;
  background-size: 71px auto;
  background-repeat: no-repeat;
  @media (max-width: 768px) {
    background-size: 50px auto;
    width: 50px;
    height: 50px;
  }
}