.header__dropdown {
  margin: -10px auto 0;
  vertical-align: middle;
  width: 200px;
  display: block;
  text-align: center;
  font-size: 13px;
  line-height: 30px;
  .dropdown__content {
    background-color: #fff;
    border-right: 1px;
    color: @darkGrey;
    left: 6px;
    padding-bottom: 7px;
    padding-top: 34px;
    position: absolute;
    top: -4px;
    width: 100%;
    z-index: 1;
    max-height: 400px;
    overflow: auto;
    &__item {
      &_selected {
        display: none;
      }
      a {
        color: @darkGrey;
        display: block;
        text-decoration: none;
      }
    }
  }
  &__selected {
    color: #fff;
    position: relative;
    z-index: 2;
    margin-left: 12px;
    .sprite;
    &:after {
      position: absolute;
      right: 12px;
      top: 8px;
      .icons;
      .svg-arrowDownWhite;
      .svg-arrowDownWhite-dims;
    }
    &:before {
      content: '';
      width: 100%;
      position: absolute;
      left: 0;
      top: -4px;
      height: 35px;
      z-index: -1;
    }
    &_active {
      color: @red;
      &:before {
        background-color: #fff;
        -webkit-transition-delay: .4s;
        -o-transition-delay: .4s;
        transition-delay: .4s;
        -webkit-transition-property: background;
        -o-transition-property: background;
        transition-property: background;
      }
      &:after {
        .icons;
        .svg-arrowDownBlack;
        .svg-arrowDownBlack-dims;
      }
    }
  }
}