.advantage {
  margin-top: 63px;
  &__title {
    margin-bottom: 85px;
    @media(max-width: 991px) {
      margin-bottom: 27px;
    }
  }
  &__row {
    margin: 60px -40px;
    @media(max-width: 1280px) {
      margin-left: -30px;
      margin-right: -30px;
    }
    @media(max-width: 991px) {
      margin: 0;
    }
    &__title {
      color: @mOrange;
      font-family: FedraSansAltProBold;
      font-size: 32px;
      letter-spacing: -0.3px;
      line-height: 26px;
      margin-top: 36px;
      @media (max-width: 991px) {
        margin-top: 50px;
      }
    }
  }
  &__descript {
    font-family: FedraSansAltProBookTF;
    font-size: 15px;
    color: @darkGrey;
    letter-spacing: 0;
    line-height: 24px;
    margin-top: 30px;
  }
}