.container_tel {
  max-width: 533px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
.tel {
  margin: 115px 0 38px;
  @media(max-width: 768px) {
    margin-bottom: 0;
    background-color: @lLight;
    padding-top: 42px;
    margin-top: 102px;
    .pull-left,
    .pull-right {
      float: none;
    }
  }
  &__link {
    display: block;
    @media(max-width: 768px) {
      margin-bottom: 30px;
    }
  }
  &_title {
    font-family: FedraSansAltProBookTF;
    display: block;
    font-size: 13px;
    color: @darkGrey;
    letter-spacing: -0.09px;
  }
  &_num {
    margin-top: 15px;
    font-family: FedraSansAltProMedTF;
    display: block;
    font-size: 24px;
    color: @dark;
    letter-spacing: 0;
    @media(max-width: 768px) {
      font-size: 22px;
    }
  }
}