.profit {
  margin-top: 150px;
  @media(max-width: 768px) {
    margin-top: 57px;
  }
  &__title {
    margin-bottom: 20px;
    @media(max-width: 768px) {
      margin-bottom: 34px;
    }
  }
  &__btn {
    margin-top: 37px;
    margin-bottom: 27px;
    @media(max-width: 768px) {
      width: 100%;
      padding-left: 0;
      padding-right: 0;
      font-size: 16px;
      letter-spacing: 0;
    }
  }
}