.footer {
  padding: 21px 0;
  background-color: @lLight;
  font-family: FedraSansAltProBookTF;
  font-size: 13px;
  color: @darkGrey;
  letter-spacing: 0;
  line-height: 22px;
  @media(max-width: 768px) {
    .pull-left,
    .pull-right {
      float: none;
    }
    .pull-right {
      margin-top: 35px;
    }
  }
}